
  @import "../assets/sass/config.scss";
  .clearfix{
    &:after,&:before{
      content: '';
      display: table;
    }
    &:after{
      clear:both;
    }
  }
  .inner-rl{padding-left: rem($inner-range);padding-right: rem($inner-range);}
  .pull-right{float:right;}
  .pull-left{float:left;}
  .ta-r{text-align: right;}
  #guesshome{
    height: 100%;
    background: url(../assets/images/guess_bg.jpg) no-repeat;
    background-size:100% auto;
  }
  .top-txt{
    height: rem(35px);
  }
  .top-title{
    height: rem(95);
    background:url(../assets/images/gs_logo.png) no-repeat center center;
    background-size:rem(316) auto;
  }
  %gs-icon{
      background:url(../assets/images/gs_icon_v2.png) no-repeat;
      background-size:rem(302) rem(1143);
  }
  .guess-func{
    height: rem(90);
    padding-right: rem(40);
    padding-left: rem(40);
    color:#fff;
    .guess-score{
      margin-top: rem(14);
      float:right;
      display: inline-block;
      width: rem(265);
      height: rem(52);
      line-height:rem(56);
      font-size: rem(25);
      color:#fff;text-align: center;
      font-weight: bold;
      padding: 0 rem(52) 0 rem(65);
      @extend %gs-icon;
      background-position: 0 rem(-970);
      position:relative;
    }
    .add-score{
      width: rem(52);
      height: rem(52);
      display: inline-block;
      position:absolute;
      right: 0;top:0;
      @extend %gs-icon;
      background-position: rem(-2) rem(-148);
    }
  }
  .guess-func{
    a{
      display: inline-block;
      @include size(70,70);
      @extend %gs-icon;
      margin-right:rem(20);
      color:transparent;
      &:first-child{
        background-position:0 rem(-310);
      }
      &:nth-child(2){
        background-position:0 rem(-233);
      }
      &:nth-child(3).active{
        background-position:0 rem(-382);
      }
      &:nth-child(3){
        background-position:rem(-166) rem(-382);
      }
    }
  }
  .guess-main{
    height: rem(790);position:relative;
  }
  .gs-result{
    position:absolute;color:#ffeeaa;
    right: rem(40);
    top:rem(40);
    @include font-dpr(20px);
    span{color:#ffcc01;@include font-dpr(22px);}
    .resultcount{
       @include font-dpr(28px);
    }
  }
  .guess-call{
    text-align: center;
    padding-top: rem(10);
    a{
      @include size(220,240);
      display: inline-block;
      background:url(../assets/images/gs_ui.png) no-repeat;
      background-size: rem(572) auto;
      &.gu-call-small{
        background-position: 0 rem(-172);
      }
      &.gu-call-baozi{
        background-position: 0 rem(-430);
      }
      &.gu-call-big{
        background-position: 0 rem(-695);
      }
      &.gu-call-small.active{
        background-position: rem(-254) rem(-172);
      }
      &.gu-call-baozi.active{
        background-position: rem(-252) rem(-430);
      }
      &.gu-call-big.active{
        background-position: rem(-254) rem(-693);
      }
    }
  }
  %sanzi-num{
    background:url(../assets/images/sourceR.png) no-repeat;
    background-size:rem(273) auto;
  }
@keyframes rocks{
    0%{transform:translateX(0);}
    10%,30%,50%,70%,90%{transform:translateX(rem(200));}
    20%,40%,60%,80%,{transform:translateX(rem(-200));}
    95%{transform:translateX(0);}
    100%{transform:translateX(0);}
}
  .gs-roller{
    @include size(370,700);
    margin: 0 auto;
    position:relative;
       -webkit-transition:all 0.2s 0.3s linear;
       transition:all 0.2s 0.3s linear;

    &.active{ 
         
          animation: rocks 2s linear 0.6s infinite;
      .gs-roller-cent{
         transform:translateY(rem(200));
         background:url(../assets/images/source2.png) no-repeat;
      background-size: rem(371) auto;
      background-position:rem(10) top;
      }


    }
    .gs-roller-cent{
      -webkit-transition:transform 0.2s linear;
       transition:transform 0.2s linear;
      height: rem(440);
      
      // 
      z-index:2;
      position:relative;
    }
    .gs-roller-bot{
      z-index:1;
      position:absolute;
      width: 100%;
      height: rem(250);
      bottom:0;
      left:0;
      background:url(../assets/images/source2.png) no-repeat;
      background-size: rem(371) auto;
      background-position:0 bottom;
    }
    .sanzi{
      display: inline-block;
      @include size(88,90);
      @extend %sanzi-num;
      position:absolute;
      &:first-child{
          top:rem(20);
          left: rem(50)
      }
      &:nth-child(2){
          top:rem(50);
          left: rem(140);
      }
      &:nth-child(3){
        right:rem(50);
        top:rem(20);
      }
      &.one{
        background-position:rem(-40) rem(-5);
      }
      &.two{
        background-position:rem(-140) rem(-5);
      }
      &.three{
        background-position:rem(-40) rem(-100);
      }
      &.four{
        background-position:rem(-140) rem(-100);
      }
      &.five{
        background-position:rem(-40) rem(-200);
      }
      &.six{
        background-position:rem(-140) rem(-200);
      }
    }
  }








