
@import "../assets/sass/config.scss";
#guessrule{
  background:#422910;
  height: 100%;
  color:#DFCCA6;
}
.guessrule-header{
  height: rem(95);
  background:url(../assets/images/dingbu.png) no-repeat;
  background-size:100% 100%;
}
.guessrule-info{
  margin: rem(30);
  padding: rem(10) rem(20);
  border: 0.02rem solid #704f2a;
  border-radius: 0.15rem;
  box-shadow: 0.03rem 0.03rem 0.03rem #1e1306 inset;
}



